body { background: #fff; }

/* Box Sizing
 * ========================================================================== */

html,
input[type="search"] {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

/* Float Clearing
 * ========================================================================== */

.clearfix:before,
.footer-widgets:before,
.nav-primary:before,
.nav-secondary:before,
.site-container:before,
.site-footer:before,
.site-header:before,
.site-inner:before,
.wrap:before {
  content: " ";
  display: table;
}

.clearfix:after,
.footer-widgets:after,
.nav-primary:after,
.nav-secondary:after,
.site-container:after,
.site-footer:after,
.site-header:after,
.site-inner:after,
.wrap:after {
  clear: both;
  content: " ";
  display: table;
}

/* Utility
 * ========================================================================== */

.mobile-hide {
  visibility: hidden;
  clip: rect(0px, 0px, 0px, 0px);
  position: absolute;
  display: none;
}

.text-center {
  text-align: center;
}

@media only screen and (min-width: 800px) {
  .mobile-hide {
    visibility: visible;
    position: relative;
    display: block;
  }

  .mobile-show {
    visibility: hidden;
    clip: rect(0px, 0px, 0px, 0px);
    position: absolute;
  }
}

/* Typography
 * ========================================================================== */

html {
  font-size: 62.5%;
}

body {
  font-family: Open Sans, "Avenir", "Helvetica", arial;
  font-size: 1.4rem;
