  -moz-transition:    all .2s cubic-bezier(0,1,.75,1);
  -ms-transition:     all .2s cubic-bezier(0,1,.75,1);
  -o-transition:      all .2s cubic-bezier(0,1,.75,1);
  transition:         all .2s cubic-bezier(0,1,.75,1);
}

/* Selection
 * ========================================================================== */

